* {
  box-sizing: border-box;
  margin: 0;
}

#app {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.page-container {
  flex: 1 0 auto;
  background-color: #dbfdf2;
}

footer {
  flex-shrink: 0;
}

.item-details-container {
  margin: 100px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
}

.card-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  padding: 1rem;
  border: none;
  border-radius: 6px;
  box-shadow: 0 0 20px #d4d4d4;
  background-color: white;
}
@media (min-width: 768px) {
  .card-container {
    width: 600px;
  }
}

.card-text-container {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.card-title {
  font-size: 2.5rem;
  color: white;
  padding-bottom: 1rem;
  font-family: "Montserrat", sans-serif;
}

.card-sub-title {
  color: white;
  font-family: "Montserrat", sans-serif;
}

.card-text {
  color: white;
  font-family: "Quicksand", sans-serif;
}

.button-inline-container {
  display: flex;
  display: inline-block;
  padding: 1rem;
}

.card-img {
  width: auto;
  border-radius: 5px;
  height: 20rem;
  object-fit: cover;
}

.card-container-horizontal {
  display: flex;
  justify-content: center;
  width: 20rem;
  flex-direction: column;
  text-align: left;
  padding: 1rem;
  border: none;
  border-radius: 6px;
  margin: 4rem;
  box-shadow: 0 0 20px #d4d4d4;
}
@media (min-width: 768px) {
  .card-container-horizontal {
    width: 41.6666666667%;
  }
}

.form-title {
  color: #333;
  font-family: #6458F5;
  font-size: x-large;
  font-weight: bold;
}

header {
  width: auto;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-image {
  width: 15rem;
  height: auto;
  object-fit: cover;
  padding-left: 10px;
}
@media (min-width: 768px) {
  .logo-image {
    padding-left: 20px;
    width: 20rem;
  }
}

.nav {
  width: auto;
}

.nav-link {
  display: none;
  color: #6458F5;
  text-decoration: none;
  font-size: 2rem;
  font-family: "Montserrat", sans-serif;
}
.nav-link:hover {
  color: #B2ACFA;
}
@media (min-width: 768px) {
  .nav-link {
    display: block;
    padding-left: 1rem;
    font-size: 1.2rem;
    font-family: "Quicksand", sans-serif;
  }
}

.hamburger {
  cursor: pointer;
  color: #6458F5;
  font-size: 2.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  padding-right: 1rem;
}
.hamburger:hover {
  color: #B2ACFA;
}
@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.close-btn {
  cursor: pointer;
  color: #6458F5;
  font-size: 3rem;
  position: absolute;
  top: 22px;
  right: 12px;
}
.close-btn:hover {
  color: #B2ACFA;
}
@media (min-width: 768px) {
  .close-btn {
    display: none;
  }
}

.nav-menu {
  height: 100%;
  display: flex;
}
@media (min-width: 768px) {
  .nav-menu {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    padding-right: 3rem;
  }
}

.nav-menu-toggle {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 50vh;
  z-index: 100;
  animation: growDown 300ms ease-in-out forwards;
  transform-origin: top center;
  position: absolute;
  left: 0;
  top: 0;
}
@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}
.nav-menu-toggle .nav-link {
  display: block;
}
@media (min-width: 768px) {
  .nav-menu-toggle {
    display: none;
  }
}

